.add-button {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
  position: relative;
  display: inline-flex;
  padding: 0;
  transition: transform .1s ease-in-out, box-shadow .1s linear;
}

.add-button:before, .add-button:after {
  content: '';
  position: absolute;
}

.add-button:before {
  left: 0;
  top: 0;
  background: linear-gradient(135deg, #E32C75 0%, #FF974F 100%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0;
  transition: opacity .1s ease-in-out, box-shadow .1s ease-in-out;
}

.add-button:after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background: url("../imgs/plus-icon.svg") no-repeat center/contain;
  z-index: 2;
}

.add-button:hover:before {
  opacity: 1;
}

.add-button:active {
  transform: scale(0.9);
}

.add-button:active:before {
  opacity: 1;
}

.add-button:focus {
  box-shadow: 0 0 20px -5px #F05D63;
}

.add-button--header-btn {
  position: absolute;
  right: 0;
  bottom: -32px;
}

@media only screen and (max-width: 567px) {
  .add-button--header-btn {
    display: none;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
    bottom: 25px;
  }
  .add-button--header-btn:active {
    transform: translateX(-50%) scale(0.9);
  }
}

.edit-btn {
  border: none;
  cursor: pointer;
  background: none;
  padding-left: 25px;
  position: relative;
  display: inline-flex;
  text-decoration: underline;
  font-family: "OpenSans-Regular", sans-serif;
  font-size: 16px;
  color: #F05D63;
}

.edit-btn:hover {
  text-decoration: none;
}

.edit-btn:before {
  content: '';
  width: 16px;
  height: 16px;
  background: url("../imgs/edit-icon.svg") no-repeat center/contain;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.edit-btn--inside-profile {
  margin-top: 15px;
}

.tooltip-menu-button {
  border: none;
  padding: 0;
  cursor: pointer;
  background: url("../imgs/dots-icon.svg") no-repeat center/4px;
  width: 24px;
  height: 24px;
  display: inline-flex;
  position: relative;
  z-index: 5;
}

.switcher {
  display: inline-flex;
}

.switcher__input {
  display: none;
}

.switcher__input:checked + .switcher__wrap .switcher__title--public {
  color: #3E3F58;
}

.switcher__input:checked + .switcher__wrap .switcher__title--private {
  color: #F05D63;
}

.switcher__input:checked + .switcher__wrap .switcher__box:before {
  transform: translate(16px, -50%);
}

.switcher__wrap {
  display: flex;
  align-items: center;
}

.switcher__box {
  width: 40px;
  height: 24px;
  background: #ED5267;
  border-radius: 24px;
  cursor: pointer;
  position: relative;
}

.switcher__box:before {
  content: '';
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  left: 3px;
  top: 50%;
  transform: translate(0, -50%);
  transition: transform .1s ease-in-out;
}

.switcher__title {
  cursor: pointer;
  font-family: "OpenSans-SemiBold", sans-serif;
  transition: color .2s linear;
}

.switcher__title--public {
  color: #F05D63;
}

.switcher__title + .switcher__box,
.switcher__box + .switcher__title {
  margin-left: 30px;
}

@media only screen and (max-width: 567px) {
  .switcher--profile-cards {
    width: 100%;
    justify-content: center;
  }
}

.hashtag {
  font-size: 16px;
