}

.account-info {
  display: inline-flex;
  align-items: center;
  position: relative;
}

.account-info__title {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.5px;
  color: #3E3F58;
  transition: color .2s linear;
}

@media only screen and (max-width: 567px) {
  .account-info__title {
    display: none;
  }
}

.account-info__title:hover {
  color: #F05D63;
}

.account-info__title:focus {
  color: #F05D63;
  text-decoration: underline;
}

.account-info__control {
  margin-left: 20px;
  position: relative;
  border: none;
  cursor: pointer;
  background: none;
  padding: 0;
  padding-right: 25px;
}

@media only screen and (max-width: 567px) {
  .account-info__control {
    margin-left: 0;
    margin-right: 15px;
  }
}

.account-info__control:before {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background: url("../imgs/arrow-down.svg") no-repeat center/contain;
  opacity: .4;
  transition: opacity .1s linear, transform .1s linear;
}

.account-info__control:hover:before, .account-info__control:focus:before {
  opacity: .85;
}

.account-info__control.active:before {
  opacity: 1;
  transform: translateY(-50%) rotate(180deg);
}

.account-info__menu {
  position: absolute;
  right: 0;
  top: calc(100% + 15px);
  min-width: 150px;
  display: none;
}

.account-info__menu.active {
  display: block;
}

.account-preview-img {
  width: 32px;
