  display: inline-flex;
  align-items: center;
  flex: none;
  border-radius: 50%;
  position: relative;
  background-color: #B2B3BB;
}

.account-preview-img img {
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  object-fit: cover;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
