  top: 20px;
  padding: 0 25px;
  font-family: "OpenSans-SemiBold", sans-serif;
  display: flex;
  align-items: center;
}

.post-item__shared-text {
  flex: none;
}

.post-item__title {
  height: 135px;
  overflow: hidden;
  font-size: 36px;
  line-height: 44px;
  font-family: "Montserrat-Light", sans-serif;
}

@media only screen and (max-width: 567px) {
  .post-item__title {
    font-size: 28px;
    line-height: 36px;
    height: 145px;
  }
}

.post-item__title a {
  color: #3E3F58;
  transition: color .2s linear;
}

.post-item__title a:hover {
  color: #F05D63;
}

.post-item__info {
  width: 100%;
  margin-top: auto;
  display: flex;
}

.post-item__author {
  display: flex;
  align-items: center;
  font-size: 14px;
  font-family: "OpenSans-SemiBold", sans-serif;
  transition: color .2s linear;
  color: #3E3F58;
  margin-right: auto;
}

.post-item__author:hover {
  color: #F05D63;
}

.post-item__share {
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  line-height: 25px;
  font-family: "OpenSans-Regular", sans-serif;
  color: #F05D63;
  padding: 0;
}

.post-item__share:hover {
  text-decoration: underline;
}

.post-statistic {
  display: flex;
  list-style: none;
}

.post-statistic__item + .post-statistic__item {
  margin-left: 15px;
}

.statistic-item {
  display: inline-flex;
  align-items: center;
  border: none;
  background: none;
  padding: 0;
  font-family: "OpenSans-SemiBold", sans-serif;
  font-size: 14px;
}

.statistic-item[type="button"] {
  cursor: pointer;
}

.statistic-item__img {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
  flex: none;
}

.burger-menu {
  display: none;
}

@media only screen and (max-width: 567px) {
  .burger-menu {
    display: block;
    padding: 0;
    width: 35px;
    height: 35px;
    border: none;
    cursor: pointer;
    background: url("../imgs/menu-icon.svg") no-repeat center/20px;
  }
}

.mobile-menu {
  display: none;
}

@media only screen and (max-width: 567px) {
  .mobile-menu {
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 999;
    visibility: hidden;
    opacity: 0;
    filter: alpha(opacity=0);
    transition: all .1s linear;
  }
}

.mobile-menu__overlay {
  backdrop-filter: blur(2px);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .2s linear;
}

.mobile-menu__content {
  position: absolute;
  right: 0;
  top: 0;
  width: calc(100% - 100px);
  height: 100%;
  overflow-y: auto;
  padding: 25px 15px;
  background: #fff;
  transform: translateX(100%);
  transition: transform .2s ease;
}

.mobile-menu .main-nav {
  display: block;
}

.mobile-menu .account-info {
  display: flex;
  flex-direction: column;
}

.mobile-menu .account-info__title {
  display: block;
}

.mobile-menu .account-info-nav {
  display: flex;
  margin: 0;
  margin-top: 10px;
}

.mobile-menu .add-button {
  display: block;
}

.mobile-menu.active {
  visibility: visible;
  opacity: 1;
  filter: alpha(opacity=100);
}

.mobile-menu.active .mobile-menu__content {
  transform: translateX(0);
}

.mobile-menu.active .mobile-menu__overlay {
  opacity: 1;
}
